export { default as Layout } from '../routes/$layout.svelte.js';
export { default as ErrorComponent } from '../routes/$error.svelte.js';

export const components = [
	() => import("../routes/index.svelte.js"),
	() => import("../routes/about.svelte.js"),
	() => import("../routes/item/[id].svelte.js"),
	() => import("../routes/user/[name].svelte.js"),
	() => import("../routes/[list]/[page].svelte.js")
];

export const routes = (d => [
	{
		// index.svelte
		pattern: /^\/$/,
		parts: [
			{ i: 0 }
		]
	},

	{
		// about.svelte
		pattern: /^\/about\/?$/,
		parts: [
			{ i: 1 }
		]
	},

	{
		// item/[id].svelte
		pattern: /^\/item\/([^/]+?)\/?$/,
		parts: [
			null,
			{ i: 2, params: match => ({ id: d(match[1]) }) }
		]
	},

	{
		// user/[name].svelte
		pattern: /^\/user\/([^/]+?)\/?$/,
		parts: [
			null,
			{ i: 3, params: match => ({ name: d(match[1]) }) }
		]
	},

	{
		// [list]/[page].svelte
		pattern: /^\/([^/]+?)\/([^/]+?)\/?$/,
		parts: [
			null,
			{ i: 4, params: match => ({ list: d(match[1]), page: d(match[2]) }) }
		]
	}
])(decodeURIComponent);